* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

.wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

nav {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  height: 120px;
}
nav ul {
  display: flex;
  list-style: none;
}
nav ul li {
  margin-right: 30px;
}
nav ul li a {
  color: #000;
}

.switch_btn {
  width: 40px;
  display: flex;
  align-items: center;
}
.switch_btn span {
  font-size: 14px;
  margin-left: 10px;
  letter-spacing: 2px;
}

.search {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  position: relative;
  width: 400px;
  margin: 0 auto;
}
.search input {
  width: 100%;
  height: 50px;
  display: block;
  border-radius: 50px;
  border: none;
  background-color: #e0e0e0;
  padding: 1em;
  font-size: 16px;
}
.search button {
  position: absolute;
  width: 25px;
  height: 25px;
  border: none;
  background: url("../img/search.svg") no-repeat;
  right: 20px;
  cursor: pointer;
}

.theme_btn {
  position: fixed;
  bottom: 50px;
  right: 5px;
  z-index: 10;
  mix-blend-mode: difference;
}

.theme {
  position: relative;
  background-color: transparent;
  border: 1px solid #9e9e9e;
  border-radius: 50%;
  color: #9e9e9e;
  width: 70px;
  height: 70px;
  padding: 1em;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 1s;
}
.theme span {
  font-size: 12px;
}
.theme.colorful {
  background: radial-gradient(circle, rgba(201, 228, 255, 0.6) 0%, rgba(114, 144, 235, 0.6) 53%, rgba(153, 0, 255, 0.6) 100%);
  border: 1px solid #a15ee8;
  color: #332546;
  font-weight: 800;
}

.pictures {
  width: 90%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  justify-items: center;
  align-items: center;
  row-gap: 120px;
  margin: 50px 0;
}
.pictures .picture, .pictures .colorful {
  width: 250px;
}
.pictures .picture p, .pictures .colorful p {
  font-size: 14px;
}
.pictures .picture a, .pictures .colorful a {
  display: block;
  overflow: hidden;
  filter: grayscale(1) contrast(110%);
  transition: filter 0.3s;
}
.pictures .picture a img, .pictures .colorful a img {
  transition: transform 0.3s;
}
.pictures .picture a:hover, .pictures .colorful a:hover {
  filter: grayscale(0);
}
.pictures .picture a:hover img, .pictures .colorful a:hover img {
  transform: scale(1.2);
}
.pictures .colorful a {
  filter: contrast(110%);
}

.morePictures {
  margin: 40px 0;
}
.morePictures button {
  cursor: pointer;
  width: 220px;
  padding: 1.2em 0;
  border: 1px solid #000;
  background-color: transparent;
  letter-spacing: 5px;
  transition: letter-spacing 0.3s;
  color: #000;
}
.morePictures button:hover {
  letter-spacing: 1px;
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000;
  height: 150px;
}
footer p {
  color: #fff;
  letter-spacing: 3px;
  font-size: 10px;
}

.about {
  width: 100%;
  text-align: center;
  height: calc(100vh - 270px);
  margin: 0 auto;
}
.about h1 {
  padding: 100px;
}
.about article {
  line-height: 50px;
  font-size: 14px;
}

@media screen and (max-width: 1500px) {
  .pictures {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    -moz-column-gap: 60px;
         column-gap: 60px;
  }
  .pictures .picture, .pictures .colorful {
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .search {
    width: 95%;
  }
  .pictures {
    grid-template-columns: 1fr 1fr;
    -moz-column-gap: 20px;
         column-gap: 20px;
  }
}/*# sourceMappingURL=style.css.map */